@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

#ecosystemsAlliances{

    margin:0 auto;
    padding: 100px 0px 0px 100px;
    background-color: #101010;
}


#ecosystemsAlliances > .page > main { width:100%; }
#ecosystemsAlliances .ecosystemsMain {

   background-color: #101010;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs{

    padding: 180px 290px 50px;
    margin:0 auto;
    overflow: hidden;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5{

    font-size: 7.5rem;
    font-weight: 500;
    color:#fff;
    font-family: "Plus Jakarta Sans", sans-serif;
    opacity: 0;
    transform: translateY(100px);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active{

    transition: 800ms;
    transform: translateY(0px);
    opacity: 1;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5 p{

   padding:0px 0px 0px 200px;
   opacity:0;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active p{

   opacity: 1;
   transition: 100ms;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs{

  padding-top:80px;
  opacity: 0;
  transform: translateY(100px);

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs h5.active ~ .subTextBoxs{

   opacity: 1;
   transform: translateY(0px);
   transition: 1s;
}
#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs p{

   font-size: 26px;
   font-weight: 600;
   color:#fff;
   padding-bottom:20px;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs span{
 
    font-size: 18px;
    font-weight: 400;
    line-height: 32px;
    color:#fff;
    display: block;
    width: 38%;
    word-break: keep-all;
    opacity: 60%;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents{

   width: calc(100% - 150px);
   height:780px;
   margin:0 auto;
   border-top:1px solid #343434;
   margin-top:50px;
   display: flex;
   align-items: center;
   justify-content: center;
   background-position: center center;
   background-repeat: no-repeat;
   background-size: 115%;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap{

    height:100%;
    margin:0 auto;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent{

   display: flex;
   align-items: center;
   justify-content: flex-end;
   padding: 34px 0px;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent:last-child{

   padding:0px;
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul{

   width: 605px;
   height: 280px;
   background-color:transparent;
   border-radius: 16px;
   margin: 0px 30px;
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li{

  padding:30px;
  height:calc(100% - 60px);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  background-color: transparent;
  border-radius: 16px;
  position: relative;
  
}
#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .opacityBack{

   width: 100%;
   height: 100%;
   background-color: #1A1A1A;
   border-radius: 16px;
   position: absolute;
   left:0;
   top:0;

}
#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li:hover .opacityBack{

   opacity: 40%;
   filter: contrast(50);
   transition: 500ms;
  
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .logo{

   height:32px;
   position: absolute;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .logo img{

   height:100%;
   filter: contrast(0);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li:hover .logo img{
   filter: contrast(100);
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts{

   margin-top:21%;
   position: absolute;

}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts p{

   color:#fff;
   font-weight: 600;
   font-size: 20px;
   line-height: 24px;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .texts span{

   font-weight: 400;
   font-size: 15px;
   line-height: 24px;
   color:#737373;
   display: block;
   padding-top:14px;
   width:73%;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .toLink{

   position: absolute;
   right:30px;
   height:40px;
   bottom:30px;
}


#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li .toLink img{

   height:100%;
   transform: translateX(0px);
   transition: 500ms;
}

#ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul li:hover .toLink img{
   transform: translateX(5px);
   transition: 500ms;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers {

   width: 100%;
   margin:0 auto;
   padding:50px 0px 150px;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap {

   width: 100%;
   height: 220px;
   margin:0 auto;
   overflow: hidden;
   
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs{

   width: 100%;
   height: 160px;
   margin:0 auto;
   overflow: hidden;
   display: flex;
   align-items: center;
   justify-content: center;
}
#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSliders{

   display: flex;
   align-items: center;
   justify-content: center;
   height: 100%;
   animation: ecosysSliderFlow01 50s linear infinite;

}
#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSlidersAddSeconds{

   animation: ecosysSliderFlow02 100s linear infinite;

}

@keyframes ecosysSliderFlow01{
   0% {
     transform: translateX(-100%);
   }
   100% {
     transform: translateX(100%);
   }
 }

 @keyframes ecosysSliderFlow02{
   0% {
     transform: translateX(100%);
   }
   100% {
     transform: translateX(-100%);
   }
 }

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecosysSlidersPaddings{

   padding-top:120px;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSliders .ecocSysSlides{

   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 10px;
   width:240px;
   height:100%;
   margin-right:12px;
   transition: 0.25s ease;
   overflow: hidden;
}
#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .ecoSysAnimationBoxs .ecosysSliders .ecocSysSlides img{

    height:100%;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .textContents {

   padding:30px 150px;
   margin: 0 auto;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .textContents ul{

   width: 100%;
   margin:0 auto;
   
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .textContents ul li{

   width: 100%;
   height: 1px;
   background-color: #212121;
   position: relative;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .textContents ul li em{

   position: absolute;
   left:0;
   top:0;
   width: 83px;
   height: 1px;
   background-color: #fff;

}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .addWidth ul li em{
   width: 197px;
}

#ecosystemsAlliances .ecosystemsMain .ecosysScrollWrappers .ecoSysAnimationWrap .textContents ul h3{

   color: #cfcfcf;
   font-weight: 400;
   font-size: 14px;
   padding:12px 0px 0px;
}

@media all and (max-width:1400px){

   #ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs{ padding: 180px 90px 50px;}
   #ecosystemsAlliances .ecosystemsMain .ecosystemsTitleboxs .subTextBoxs span{
      width:70%;
   }

   #ecosystemsAlliances .ecosystemsMain .ecosystemsContents .ecosysCompaniesWrap .companiesContent ul{

      width:405px;
   }
}
